* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	color: black;
	font-family: "Courier New", Courier, monospace;
}

html,
body,
div#root {
	width: 100vw;
	height: 100vh;
	overflow-x: hidden;
}

div#root {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

ul li {
	list-style: none;
}

button,
input,
textarea,
a {
	outline: none;
}

textarea {
	overflow: auto;
	vertical-align: top;
	resize: vertical;
}

button,
a,
label {
	cursor: pointer;
}

a {
	text-decoration: none;
}